.formulario-form {
    display: flex;
    width: 80%;
    align-items: center;
    margin-left: 35%;
    color: #fff;
    font-family: 'Lato', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding-bottom: 5%;
}

table {
    width: 70%;
}

.botao-enviar {
    font-weight: 600;
    font-family: 'Lato', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1.2rem;
}

.tabela-topo {
    font-weight: 900;
    font-family: 'Lato', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.tabela-descricao {
    font-size: 0.9rem;
}

table {
    width: 100%;
}

th, td {
    padding: 0.6rem;
}


.inserir{
    border-bottom: 2px solid white;
    background-color: transparent;
}

.inserir-cpf{
    border-bottom: 2px solid #f59e0b;
    background-color: transparent;
}

.inserir::placeholder{
    color:#ffffff;
}


.texto-input{
    display: flex;
    flex-direction: column;
    text-align: left;
}



/* botao select */

.segment{
    width: 100%;
}

.segment option {
    margin: 40px;
    background: rgba(0, 0, 0, 0.3);
    color: #fff;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
}


.segment option[value="vazio"] {
    background: #ffffff;
}

.segment option[value="Manager"] {
    background: #f59e0b;
}

.segment option[value="Reseller"] {
    background: #d97706;
}

.segment option[value="Distributor"] {
    background: #f59e0b;
}


/*  media query's */

@media (max-height: 600px) {
    form{
        min-height: 200px;
        height: 60vh;
    }
}

@media (max-width: 400px) {
    form{
        align-items: left;
        margin-left: -50%;
    }
    table {
        margin-left: -38%;
        flex-wrap: wrap;
        font-size: 0.55rem;
    }
    .texto-cpf{
       font-size: 0.55rem;
    }
    .tabela-topo {
        font-size: 0.8em;
        margin-left: -35%;
        text-align: left;
    }
    .tabela-descricao {
        margin-left: -35%;
        font-size: 0.6rem;
        text-align: left;
    }
}


@media (max-width: 600px) {
    form{
        align-items: left;
        margin-left: -40%;

    }
}
@media (max-width: 1100px) {
    form{
        align-items: center;
        margin-left: 27%;
    }
}